import React from 'react';
import styles from '../style/EmojiResultRow.css';

class EmojiResultsRow extends React.Component {
  render() {
    const src = `//cdn.jsdelivr.net/emojione/assets/png/${this.props.data.unicode}.png`;
    return (
      <div
        className={styles.row}
      >
        <img src={src} />
        {' '}
        <span className={styles.title} >
          {this.props.data.title}
        </span>
        <span className={styles.character}>
          {this.props.data.character}
        </span>
      </div>
    );
  }
}
EmojiResultsRow.propTypes = {
  data: React.PropTypes.shape({
    character: React.PropTypes.string,
    shortname: React.PropTypes.string,
    unicode: React.PropTypes.string,
    title: React.PropTypes.string,
  }),
};
export default EmojiResultsRow;



