import emojiStrategy from '../node_modules/emojione/emoji_strategy.json';

const allEmojiData = Object.values(emojiStrategy);

function capitalizeWord(word) {
  if (word.length < 2) {
    return word;
  }
  return word[0].toUpperCase() + word.substr(1);
}

function humanReadable(underscore) {
  return underscore
  .split('_')
  .map(word => capitalizeWord(word))
  .join(' ');
}

// convert from the format that emojione uses to the format we prefer
function cleanEmoji(emojiData) {
  return {
    unicode: emojiData.unicode,
    shortname: emojiData.shortname,
    character: String.fromCodePoint(parseInt(emojiData.unicode, 16)),
    title: humanReadable(emojiData.shortname.replace(/\:/g, '')),
  };
}

function emojiMatch(str, emojiData) {
  if (!emojiData.unicode) {
    return false;
  }

  if (typeof emojiData.shortname === 'string') {
    if (emojiData.shortname.indexOf(str) > -1) {
      return true;
    }
  }

  if (typeof emojiData.keywords === 'string') {
    if (emojiData.keywords.indexOf(str) > -1) {
      return true;
    }
  }

  return false;
}

function emojiLookup(str, maxResults) {
  return allEmojiData.filter((emojiData) => {
    return emojiMatch(str.toLowerCase(), emojiData);
  })
  .map(cleanEmoji)
  .slice(0, maxResults);
}
export default emojiLookup;

if (module.parent === null) {
  console.log('fire:', emojiLookup('fire'));
}



