import React from 'react';
import Header from './Header';
import SearchInput from './SearchInput';
import EmojiResults from './EmojiResults';
import emojiLookup from '../emojiLookup';

class Root extends React.Component {
  constructor() {
    super();
    this.state = {
      matches: emojiLookup('', 20),
    };
  }

  render() {
    return (
      <div>
        <Header />
        <SearchInput
          textChanged={this.handleSearchInputChange.bind(this)}
        />
        <EmojiResults
          matches={this.state.matches}
        />
      </div>
    );
  }

  handleSearchInputChange(searchText) {
    this.setState({
      matches: emojiLookup(searchText, 20),
    });
  }
}
export default Root;



